.testimonials-section {
    padding: 80px 0;
    background-color: #142613;
    position: relative;
}

.testimonials-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.testimonials-header {
    text-align: center;
    margin-bottom: 50px;
}

.testimonials-title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 15px;
    font-weight: 600;
}

.testimonials-subtitle {
    font-size: 1rem;
    color: #fff;
    line-height: 1.6;
    max-width: 800px;
    margin: 0 auto;
}

.view-all-button {
    padding: 8px 16px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: background-color 0.3s ease;
}

.view-all-button:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.testimonials-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.testimonial-card {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    padding: 30px;
    display: flex;
    flex-direction: column;
}

.rating {
    margin-bottom: 20px;
}

.star {
    color: #FFD700;
    font-size: 1.2rem;
    margin-right: 5px;
}

.testimonial-title {
    color: #fff;
    font-size: 1.3rem;
    margin-bottom: 15px;
    font-weight: 600;
}

.testimonial-text {
    font-size: 1rem;
    line-height: 1.6;
    color: #999;
    margin-bottom: 30px;
    flex-grow: 1;
}

.testimonial-author {
    display: flex;
    align-items: center;
    gap: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 20px;
}

.author-info {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.author-name {
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
}

.author-location {
    color: #666;
    font-size: 0.9rem;
}

/* 響應式設計 */
@media (max-width: 1024px) {
    .testimonials-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 25px;
    }
}

@media (max-width: 768px) {
    .testimonials-section {
        padding: 60px 0;
    }

    .testimonials-header {
        margin-bottom: 40px;
    }

    .testimonials-title {
        font-size: 2rem;
    }

    .view-all-button {
        width: 100%;
    }

    .testimonials-grid {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    .testimonial-card {
        padding: 25px;
    }
}

@media (max-width: 480px) {
    .testimonials-section {
        padding: 40px 0;
    }

    .testimonials-title {
        font-size: 1.8rem;
    }

    .testimonial-card {
        padding: 20px;
    }
} 